#project {
  display: flex;
  justify-content: center;
}

.project__contents {
  width: 1200px;
  height: 700px;

  transform: translateY(100px);

  /* background-color: rgb(116, 156, 138); */
  /* background-color: rgb(132, 132, 143); */
  /* background-color: rgb(160, 172, 132); */
  background-color: rgb(34, 51, 51, 1);
  border-radius: 20px;

  text-align: center;

  overflow: auto;
}

.project__contents .project__contents__object {
  display: inline-block;

  width: 25%;
  height: 45%;

  /* margin: auto; */

  margin-left: 40px;
  margin-right: 40px;
  margin-top: 20px;
  margin-bottom: 20px;

  background-color: white;
}

.project__contents__object .subject {
  font-size: 30px;
  text-align: center;
  height: 50%;
}
